<template>
  <div>
    <div class="d-flex align-items-center pl-5 p-3 mb-2 border">
      <b-button
        @click="getVocabulary"
        variant="danger"
        class="mx-4"
        :disabled="isBusy"
      >
        Suppresion des termes
      </b-button>
      <b-form-group class="align-items-center">
        <b-form-checkbox
          v-model="recursive"
          name="checkbox-1"
          :value="true"
          :unchecked-value="false"
        >
          Recursive ...
        </b-form-checkbox>
      </b-form-group>
      <b-form-group
        class="align-items-center"
        label="Nombre de termes à supprimer"
      >
        <b-form-input v-model="NumberTermDelete" type="number"></b-form-input>
      </b-form-group>
      <div class="mx-5" v-if="recursive">
        <b-icon icon="three-dots" animation="cylon" font-scale="2"></b-icon>
      </div>
    </div>
    <div v-for="(rows, i) in delteTermes" :key="i">
      {{ rows.time / 1000 }} s
      <hr />
      <span v-for="(row, k) in rows.terms" :key="k"> {{ row.text }} | </span>
    </div>
  </div>
</template>

<script>
import { termsTaxo } from "drupal-vuejs";
import utilities from "drupal-vuejs/src/App/utilities";
utilities.TestDomain = "http://v2lesroisdelareno.kksa";
export default {
  name: "DeleteTerms",
  data() {
    return {
      delteTermes: [],
      recursive: false,
      NumberTermDelete: 1,
      isBusy: false,
    };
  },
  mounted() {
    // this.getVocabulary();
  },
  methods: {
    getVocabulary() {
      const start = new Date();

      const vid = "departement_de_france";
      const terms = new termsTaxo(vid);
      const DeleteProgess = () => {
        this.isBusy = true;
        terms.get().then(() => {
          var t = terms.getOptions();
          const tids = [];
          t.forEach((term) => {
            if (tids.length < this.NumberTermDelete) tids.push(term.value);
          });
          utilities
            .post("/import-entity-progress/deleteterms", {
              vid: vid,
              tids: tids,
            })
            .then(() => {
              const time = new Date() - start;
              t.splice(tids.length, t.length - tids.length);
              this.delteTermes.push({
                terms: t,
                time: time,
              });
              if (this.recursive) {
                this.isBusy = false;
                setTimeout(() => {
                  DeleteProgess();
                }, 10000);
              }
            });
        });
      };
      DeleteProgess();
    },
  },
};
</script>
